.App {
  width: inherit;
  height: inherit;
  overflow: auto;
  background-color: black;
  text-align: center;
}

.App h2 {
  font-size: 2rem;
  color: white;
  font-weight: 700;
  margin: 7rem 0 2rem 0;
  padding: 0 2rem;
}

.App .error {
  font-size: 1rem;
  color: red;
  margin: 3rem 0;
}

h3 {
  color: white;
  margin-bottom: 2rem;
  font-size: 1.5rem;
}

.survey {
  display: grid;
  grid-template-columns: 1fr 1fr;
  max-width: 1024px;
  margin: 0 auto;
  align-items: center;
  justify-items: center;
}

.survey div input {
  display: none;
}

.survey div label {
  color: rebeccapurple;
}

.survey div input#dl + label::before {
  content: "";
  display: block;
  height: 320px;
  width: 320px;
  background-size: 100% 100%;
  background-image: url(data:image/svg+xml;base64,);
}

.survey div input#l + label::before {
  content: "";
  display: block;
  height: 320px;
  width: 320px;
  background-size: 100% 100%;
  border: 2px solid transparent;
  background-image: url(data:image/svg+xml;base64,);
}

.survey div input#l:checked + label::before,
.survey div input#dl:checked + label::before {
  border: 2px solid #caa945;
  border-radius: 300px;
}

[type="text"] {
  padding: 0.5rem 1rem;
  font-family: "Bungee", cursive;
  font-size: 1.25rem;
  border: 2px solid #caa945;
  outline: none;
  width: 320px;
  margin-bottom: 2rem;
}

button {
  font-family: "Bungee", cursive;
  font-size: 1.25rem;
  padding: 0.5rem 1rem;
  border: 2px solid #caa945;
  background: #caa945;
  color: white;
  border-radius: 50px;
  margin-top: 2rem;
}

h1 {
  color: white;
  font-size: 1.5rem;
  margin-top: 20rem;
  color: #caa945;
}

@media only screen and (max-width: 600px) {
  .survey {
    grid-template-rows: 1fr 1fr;
    grid-template-columns: auto;
  }
}
